<template>
  <div class="settlement-detail">
    <div class="settlement-detail-total-container">
      <div class="settlement-detail-total">
        <van-row>
          <van-col span="6">
            <div class="total-image">
              <img src="../../assets/images/img.png" alt="">
            </div>
          </van-col>
          <van-col span="18">
            <p>{{ total.title }}</p>
            <p class="total-prod-number">{{ total.number }}</p>
          </van-col>
        </van-row>
        <div class="total-money">
          <span>￥{{ totalPrice }}</span>
        </div>
      </div>
    </div>
    <div class="production-list">
      <p class="production-list-title">商品详情</p>
      <template v-for="(item, i) in items">
        <van-card
          :key="i"
          :num="item.sum"
          :price="item.price"
          :desc="item | getDesc"
          :title="item.name"
          :thumb="item.img"
        >
        </van-card>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'settlement-detail',
  filters: {
    getDesc(item) {
      return [item.jfcode, item.product_id, item.brand_name].join(', ');
    }
  },
  data () {
    return {
      items: [],
      total: {}
    }
  },
  created () {
    const { query } = this.$route;
    this.getItems(query)
  },
  methods: {
    getItems (params) {
      // 假数据
      // const mockData = {
      //   "code": 1,
      //   "msg": "获取成功",
      //   "data": [
      //     {
      //       "name": "火花塞PFR7S8EG",
      //       "jfcode": "1675",
      //       "img": "https://zpkoss.oss-cn-shenzhen.aliyuncs.com/goodsimg/pic/181b1f38677f1f5f74cd390ac67655ff_1_1.jpg",
      //       "product_id": "PFR7S8EG",
      //       "brand_name": "NGK",
      //       "unit_name": "支",
      //       "id": 1,
      //       "offer_id": 1,
      //       "goods_id": 3732,
      //       "price": 48,
      //       "sum": 1
      //     },
      //     {
      //       "name": "火花塞PFR7S8EG",
      //       "jfcode": "1675",
      //       "img": "https://zpkoss.oss-cn-shenzhen.aliyuncs.com/goodsimg/pic/181b1f38677f1f5f74cd390ac67655ff_1_1.jpg",
      //       "product_id": "PFR7S8EG",
      //       "brand_name": "NGK",
      //       "unit_name": "支",
      //       "id": 1,
      //       "offer_id": 1,
      //       "goods_id": 3732,
      //       "price": 48,
      //       "sum": 1
      //     }
      //   ],
      //   "list": {
      //     "id": 1,
      //     "number": "BJD202105020001",
      //     "user_id": 3002,
      //     "producer_user": 2131,
      //     "create_time": "2021-05-02 13:28:39",
      //     "type": 1,
      //     "rem": "",
      //     "open_id": 2131,
      //     "title": "米其林轮胎泰尔汽车服务中心"
      //   },
      //   "total_price": 48
      // };
      // console.log('mockData ==>', mockData)
      // if (mockData.code === 1) {
      //   this.dealData(mockData);
      // }
      this.$https({
        url: '//admin.zpkauto.com/index/index/index',
        method: 'GET',
        params
      }).then(({ data = {}, status } = {}) => {
        console.log('data==>', data)
        if (status === 200 && data.code === 1) {
          this.dealData(data);
        }
      });
    },
    dealData(data) {
      this.items = data.data || []
      this.total = data.list || {}
      this.totalPrice = data.total_price;
    }
  }
}
</script>

<style scoped>
.settlement-detail {
  font-size: 0.24rem;
  padding: 0.2rem;
}
.settlement-detail-total {
  padding: 0.16rem;
  box-shadow: 0 10px 20px #ececec;
  border-radius: 0.1rem;
  overflow: hidden;
  position: relative;
}
/*.settlement-detail-total-container {*/
/*  background-image: linear-gradient(#e66465, #9198e5);*/
/*}*/
.total-image {
  width: 1.8rem;
  height: 1.8rem;
  text-align: center;
  padding: 0.2rem;
  box-sizing: border-box;
}
.total-image img {
  width: 100%;
  height: auto;
}
.total-money {
  position: absolute;
  color: #f6a231;
  font-size: 0.3rem;
  bottom: 0.4rem;
  right: 0.2rem;
  text-align: right;
  display: inline-block;
}
.total-prod-number {
  color: #999;
}
.total-money span {
}
.production-list {
  box-shadow: 0 -10px 20px #ececec;
  overflow: hidden;
  border-radius: 0.1rem;
  margin-top: 0.3rem;
}
.production-list-title {
  font-size: 0.26rem;
  color: #999;
  padding: 0.2rem 0.2rem;
  background: #fafafa;
  margin: 0;
}
.production-list /deep/ .van-card {
  margin-top: 0;
  border-top: 1px solid #ececec;
}
</style>



