import Vue from 'vue'
import Router from 'vue-router'
import SettlementDetail from '@/pages/production/settlement-detail.vue'
import NotFound from '@/pages/error/404.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/settlement-detail'
    },
    {
      path: '/settlement-detail',
      name: 'SettlementDetail',
      component: SettlementDetail
    },
    {
      path: '/404',
      name: 'NotFound',
      component: NotFound
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})



